$small: 300px;
$medium: 900px;

footer {  
  
  @media screen and (max-width: $small) {
    display: grid;
    place-items: center;
    grid-template-columns: 1fr 1fr 1fr;
  }
  @media screen and (min-width: $medium) {
    display: flex;
    align-items: center;
    padding: 1rem;
  }
  padding: 5rem;
  font-size: .8rem;
  color: darken($color: white, $amount: 30);
  background-color: lighten($color: black, $amount: 20);
  margin-top: 2rem;
  

}